let setupWriter = (typewriter, minTypeSpeed, maxTypeSpeed) => {
    var max = minTypeSpeed;
    var min = maxTypeSpeed;
    var i = 0;
    var tag = ""
    var begun = false;
    var span;
    var oldI;
    var typeSpeed = Math.floor(Math.random() * (maxTypeSpeed - minTypeSpeed + 1) + minTypeSpeed);
    var text = "";
    text = typewriter.innerHTML;
    typewriter.innerHTML = "";

    setInterval(function () {
        typeSpeed = Math.floor(Math.random() * (max - min + 1) + min);
        if (text[i] != undefined) {
            if (text[i] === "<" || begun) {
                if (!begun) {
                    span = document.createElement("span");
                    oldI = i;
                    i = text.indexOf(">", i);
                    var className = "";
                    for (let z = oldI; z < i + 1; z++) {
                        className += text[z];
                    }
                    span.innerHTML = className;
                    tag = span.firstChild;
                    typewriter.appendChild(span);
                }
                begun = true;
                i += 1;
                tag.innerHTML += text[i];
                if (text[i + 1] === "<") {
                    i = text.indexOf(">", i) + 1;
                    begun = false;
                }
            } else if (!begun) {
                typewriter.innerHTML += text[i];
                i += 1;
            }
        }
    }, typeSpeed);
};

//Get typewriter element
export default {
    setupWriter
}